.personal-data {
  .org-logo {
    display: inline-block;
    vertical-align: middle;
    margin-right: 12px;
    width: 48px;
    height: 48px;
    background-position: center;
    background-size: cover;
    border-radius: $border-radius; }

  .small-button {
    display: block;
    max-width: 96px;
    border-radius: 20px; }

  button.small-button {
    min-width: 96px;
    max-width: none; }

  .cta-button {
    display: block;
    width: 100%; }

  .note {
    display: inline-block;
    @extend %font-size-ui-s;
    line-height: 1.4;
    color: $color-gray; } }
