.app-download {
  position: relative;
  min-height: 100vh;
  background: radial-gradient(circle at center, $sub-color, $main-color);

  .container {
    max-width: 960px;
    min-height: 100vh;

    @include tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 72px; }

    &::after {
      display: none; } }

  .content {
    margin: 48px 0;
    text-align: center;

    @include tablet {
      text-align: left; }

    .logo {
      display: inline-block;
      width: 147px;
      height: 34px;
      background: resolve('app-logo.png') center no-repeat;
      background-size: contain; }

    .slogan {
      font-size: 28px;
      font-weight: 500;
      color: white;

      @include tablet {
        font-size: 36px; } }

    .download-btn {
      margin-top: 12px;

      a {
        display: inline-block;
        @extend %button;
        @extend %font-size-ui-m;
        padding: 8px 36px;
        color: $main-color;
        background-color: $base-color-white;
        border-radius: 24px; }

      a + a {
        margin-left: 12px; } } }

  .app-screen {
    max-width: 261px;
    margin: 48px auto;

    img {
      display: block;
      margin: 0; }

    @include tablet {
      margin-right: 0; } } }

.app-footer {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin-bottom: 12px;
  text-align: center;
  @entend %font-size-ui-s {}
  font-weight: 500;
  color: $base-color-white; }
